<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="addOrg">新增机构</el-button>
            </div>
            <div>
                <el-input
                    placeholder="请输入机构名称"
                    size="small"
                    style="width: 200px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="机构性质"
                           style="width: 140px">
                    <el-option
                        v-for="(v,k) in $Cfg.postType"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <CityCascader v-model="cityList" style="display: inline-block"></CityCascader>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="发单权限"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small" @click="refresh = !refresh">重置</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="date"
                label="机构名称">
            </el-table-column>
            <el-table-column
                prop="name"
                label="机构性质"
                width="180">
            </el-table-column>
            <el-table-column
                prop="address"
                label="所在城市">
            </el-table-column>
            <el-table-column
                prop="address"
                label="联系人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="手机号">
            </el-table-column>
            <el-table-column
                prop="address"
                label="固定电话">
            </el-table-column>
            <el-table-column
                prop="address"
                label="资质证书">
            </el-table-column>
            <el-table-column
                prop="address"
                label="发单权限">
            </el-table-column>
            <el-table-column
                label="操作"
                width="210">
                <template slot-scope="scope">
                    <el-button @click="checkOrg(scope.row)" type="info" size="small"
                    >预览
                    </el-button>
                    <el-button @click="editOrg(scope.row)" type="primary" size="small"
                    >修改
                    </el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                    >删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import CityCascader from '@/components/CityCascader/CityCascader'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                cityList: [],
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            addOrg() {
                this.$router.push('/org_add_3')
            },
            editOrg() {
                this.$router.push('/org_edit_3')
            },
            checkOrg() {
                this.$router.push('/org_check_3')
            },
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {
                    }
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, CityCascader
        }
    }
</script>
